.App {
  position: absolute;
  top:0;
  bottom:0;
  right:0;
  left:0;
  background: -webkit-gradient(linear, left top, right bottom, from(#ff4e50), to(#f9d423));
  background: -webkit-linear-gradient(left top, #ff4e50, #f9d423);
  background: -o-linear-gradient(left top, #ff4e50, #f9d423);
  background: linear-gradient(left top, #ff4e50, #f9d423);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.settings-background {
  position: absolute;
  top:0;
  bottom:0;
  right:0;
  left:0;
  background: rgba(0,0,0, 0.9);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.start, .settings {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 7rem;
}

form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  color:#fff;
}

select {
  display: -ms-flexbox;
  display: flex;
  color:#ff4e50;
  font-size: 1.6rem;
  background-color: #fff;
  width:75px;
  height: 50px;
  border-radius: 5px;
  border:none;
}

select:focus {
  outline:0;
}

option {
  outline:0;
}

h1 {
  color:#fff;
  font-size: 7.5rem;
  font-family: 'Cookie', cursive;
  margin-bottom: 20px;
}

.dice {
  display: -ms-flexbox;
  display: flex;
  background: #fff;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width:200px;
  height: 200px;
  border-radius: 20px;
}

.dot-container {
  width: 130px;
  height: 150px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.dot {
  width: 40px;
  height: 40px;
  background-color: #000;
  border: 1px solid #222;
  border-radius: 1000px;
}

.no-dot {
  width: 40px;
  height: 40px;
  /*border: 1px solid #222;*/
  border-radius: 1000px;
}

.total-container {
  display: -ms-flexbox;
  display: flex;
  background: rgba(0,0,0, 0.65);
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 445px;
  height: 200px;
  border-radius: 20px;
}

.total {
  width: 133px;
  height: 169px;
  color:#fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack:center;
      justify-content:center;
  -ms-flex-align: center;
      align-items: center;
}

.total-text {
font-size: 8rem;
line-height: 8rem;
margin: 0;
}

button {
  color:#fff;
  font-size: 1.6rem;
  background-color: #ff4e50;
  width:165px;
  height: 50px;
  border-radius: 5px;
  border:none;
  margin-top: 50px;
  -webkit-transition: .25s;
  -o-transition: .25s;
  transition: .25s;
}

button:hover {
  background-color: #D9282A;
  -webkit-transition: .25s;
  -o-transition: .25s;
  transition: .25s;
}

button:focus {
  outline: 0;
}

.cog {
  position: absolute;
  right:30px;
  top:25px;
  color: #000;
  opacity: 0.6;
  -webkit-transition: .25s;
  -o-transition: .25s;
  transition: .25s;
}

.cog:hover {
    opacity: .9;
    color:#000;
    -webkit-transition: .25s;
    -o-transition: .25s;
    transition: .25s;
}



